<template>
  <div class="name-page">
    <h1>VueÏóêÏÑú ReactÏôÄ Îç∞Ïù¥ÌÑ∞ ÍµêÌôò</h1>

    <!-- ‚úÖ ReactÏóêÏÑú Î∞õÏùÄ name Í∞í -->
    <p>ReactÏóêÏÑú Ï†ÑÎã¨Îêú name Í∞í: {{ receivedName }}</p>

    <!-- ‚úÖ VueÏóêÏÑú ÏûÖÎ†•ÌïòÎäî name -->
    <input type="text" v-model="vueName" placeholder="VueÏóêÏÑú Ïù¥Î¶Ñ ÏûÖÎ†•" />
    <button @click="sendNameToReact">ReactÎ°ú name Ï†ÑÏÜ°</button>

    <!-- Î©îÏù∏ ÌéòÏù¥ÏßÄÎ°ú ÎèåÏïÑÍ∞ÄÎäî Î≤ÑÌäº -->
    <button @click="goBack">Go Back</button>
  </div>
</template>

<script>
export default {
  name: "NamePage",
  data() {
    return {
      receivedName: "", // ‚úÖ ReactÏóêÏÑú Ï†ÑÎã¨Îêú name Í∞í
      vueName: "" // ‚úÖ VueÏóêÏÑú ÏûÖÎ†•Ìïú name Í∞í
    };
  },
  methods: {
    goBack() {
      this.$router.push("/");
    },
    sendNameToReact() {
      console.log("üü¢ VueÏóêÏÑú ReactÎ°ú name Í∞í Ï†ÑÏÜ°:", this.vueName);
      localStorage.setItem("vue-name", this.vueName); // ‚úÖ VueÏóêÏÑú ÏûÖÎ†•Ìïú Í∞í Ï†ÄÏû•
      window.dispatchEvent(new CustomEvent("vue-name-updated", { detail: this.vueName }));
    },
    handleReactNameUpdate(event) {
      console.log("üü¢ ReactÏóêÏÑú name Í∞í ÏóÖÎç∞Ïù¥Ìä∏Îê®:", event.detail);
      this.receivedName = event.detail;
      localStorage.setItem("react-name", event.detail); // ‚úÖ ReactÏóêÏÑú Î∞õÏùÄ Í∞í Ï†ÄÏû•
    },
    handleStorageChange(event) {
      if (event.key === "react-name") {
        console.log("üü¢ localStorageÏóêÏÑú ReactÏùò name Í∞í Î≥ÄÍ≤Ω Í∞êÏßÄ:", event.newValue);
        this.receivedName = event.newValue || "";
      }
    }
  },
  mounted() {
    // ‚úÖ ReactÏóêÏÑú Î≥¥ÎÇ∏ name Í∞í Î∞õÍ∏∞ (localStorageÏóêÏÑú Î∂àÎü¨Ïò§Í∏∞)
    window.addEventListener("update-name", this.handleReactNameUpdate);

    const savedReactName = localStorage.getItem("react-name");
    if (savedReactName) {
      this.receivedName = savedReactName;
    }

    const savedVueName = localStorage.getItem("vue-name");
    if (savedVueName) {
      this.vueName = savedVueName;
    }

    // ‚úÖ localStorage Î≥ÄÍ≤Ω Í∞êÏßÄ (ReactÏóêÏÑú Î≥ÄÍ≤Ω Ïãú VueÏóêÏÑú Î∞òÏòÅ)
    window.addEventListener("storage", this.handleStorageChange);
  },
  beforeDestroy() {
    window.removeEventListener("update-name", this.handleReactNameUpdate);
    window.removeEventListener("storage", this.handleStorageChange);
  }
};
</script>


<style scoped>
.name-page {
  text-align: center;
  padding: 50px;
}

h1 {
  font-size: 2em;
  color: #333;
}

p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

input {
  font-size: 1em;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

button {
  font-size: 1em;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #3498db;
  color: white;
  border: none;
  cursor: pointer;
  margin: 10px;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #2980b9;
}
</style>
